.component.input {
	width:            unit(@inputWidth, px);
	height:           unit(@inputHeight, px);
	border:           unit(@inputBorder, px) solid green;
	font-size:        unit(@inputFontSize, px);
	color:            #000;
	background-color: #fff;
	display:          table;
	table-layout:     fixed;

	> .body {
		cursor:         text;
		display:        table-cell;
		vertical-align: middle;
		padding:        0 unit(@inputPaddingX, px);
		overflow:       hidden;
		white-space:    nowrap;

		> .char, > .placeholder, > .caret {
			display:        inline-block;
			vertical-align: middle;
		}

		> .caret {
			height:      80%;
			width:       2px;
			margin-left: -2px;
		}

		> .placeholder {
			color: #999;
		}
	}

	&.focus {
		> .body {
			> .caret {
				background-color:                  #000000;
				-webkit-animation:                 inputCursor 1s;
				-webkit-animation-iteration-count: infinite;
			}
		}
	}
}

@-webkit-keyframes inputCursor {
	from {
		background-color: #000000;
	}
	to {
		background-color: transparent;
	}
}
