		vertical-align: top;
		//height: 100%;
		//display: inline-block;
		> div {
			//height: unit(@height - @availTop - @availBottom, px);
			padding: 0;
			background-color: black;

			.component {
				&.button .menu {
					//background-image: data-uri('@{pathImg}/menu.png');
				}

				&.panel {
					//padding : unit(@panelPaddingY, px) unit(@panelPaddingX, px);
					//background-color: #222;
					margin-bottom: unit(@panelPaddingY, px);

					.component.panel {
						//margin-top: unit(@panelPaddingY, px);
						background-color: #111;
					}

					.component.progressBar {
						//margin-top: unit(@panelPaddingY, px);
					}
				}
				&.panel:last-child {
