				}
			}
		}
	}
}

#pageMainTabGridMain {
	.title {
		margin-bottom: 1%;
	}
}
#pageMainTabListSimple, #pageMainTabListCustom {
	display: inline-block;
	width: 40%;
}
#pageMainTabListSimple, #pageMainTabListCustom {
	vertical-align: top;
	> table {
		width: 100%;

		.scroll {
			width: 1%;
			text-align: right;
		}
	}
}

#pageMainTabProgressBarStyle {
	.component.progressBar {
		height: 10px;
		background-color: #ffffff;
		.value {
			background-color: green;
		}
	}
}

.component.widget {
	width: 100px;
	height: 100px;
}

#pageMainTabWidgetW1 {
	background-color: red;
}
#pageMainTabWidgetW2 {
	background-color: green;
}
#pageMainTabWidgetW3 {
	background-color: blue;
	right: 0;
}
