#pageAccount {
	& > table {
		width: 100%;
		height: 100%;
	}

	.header, .footer {
		height: 40px;
		background-color: #FAFAFA;
		padding: 0 unit(@fontSize, px);
		color: #06C;
	}

	.header {
		font-size: 120%;
	}

	.body {
		border: 1px solid #eee;
		text-align: center;
		//margin-top  : unit(@fontSize * 2, px);
		//line-height : unit(@height - (@availTop + @availBottom) - @fontSize * 4, px);
		//font-size: 160%;

		.step {
			display: inline-block;
			border: 1px solid #eee;
			background-color: #fff;
			text-align: left;

			.title {
				padding: 10px;
				background-color: #FAFAFA;
				border-bottom: 1px solid #eee;
				font-size: 140%;
			}

			.content, .buttons {
				padding: 10px;
			}

			.buttons {
				border-top: 1px solid #eee;
				background-color: #FAFAFA;
				text-align: right;
			}
		}

		td.radio {
			padding: 4px 5px 4px 0;
		}

		input.server {
			width: 100%;
			height: 28px;
			padding: 3px 5px;
			font-size: unit(@fontSize, px);
		}
	}

	.footer {
		//padding: unit(@fontSize, px);
		//line-height   : unit(@fontSize * 2, px);
		text-align: right;
		//font-size     : 80%;
		color: grey;
		.value {
			color: black;
			padding-left: unit(@fontSize/2, px);
		}
	}
}

