.component {
	outline:        1px dashed #005000;
	outline-offset: -1px;

	&.page {
		outline: none;
	}

	&.focus {
		outline: 1px dashed red;
	}

	&.list {
		> .item {
			outline:        1px dashed #000000;
			outline-offset: -1px;

			&.focus {
				outline:        1px dashed orange;
				outline-offset: -1px;
			}
		}
	}

	&.grid {
		td.item {
			outline:        1px dashed #000000;
			outline-offset: -1px;

			&.focus {
				outline:        1px dashed orange;
				outline-offset: -1px;
			}
		}
	}

	&.input > .body {
		outline: 1px dashed #000;
	}
	&.focus.input > .body {
		outline: 1px dashed red;
	}
}

